<script setup>
const props = defineProps({
	text: {
		type: String,
		required: true,
	},
	reverseSideText: {
		type: String,
		required: true,
	},
	isCorrect: {
		type: Boolean,
	},
	minHeight: {
		type: String,
		default: '15vw',
	},
	isChecked: {
		type: Boolean,
		default: false,
	},
})

const emit = defineEmits(['dragstart', 'dragend'])

const onDragStart = event => {
	event.dataTransfer.setData('text', props.text)
	emit('dragstart', props.text)
}

const onDragEnd = () => {
	emit('dragend')
}
</script>

<template>
	<Transition name="flip" mode="out-in">
		<div
			v-if="!isChecked"
			:style="{ minHeight: minHeight }"
			draggable="true"
			@dragstart="onDragStart"
			@dragend="onDragEnd"
			class="container"
		>
			{{ text }}
		</div>

		<div
			:class="{ correct: isCorrect, uncorrect: !isCorrect }"
			:style="{ height: minHeight }"
			class="container"
			v-else
		>
			{{ reverseSideText }}
		</div>
	</Transition>
</template>

<style scoped>
.container {
	text-align: center;
	background-color: #dbdbdb;
	padding: 2vw;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	cursor: grab;
	font-size: 1em;
}

.correct {
	background-color: #e2f0d9;
	cursor: no-drop;
	hyphens: auto;
}

.uncorrect {
	background-color: #ffe699;
	cursor: no-drop;
	hyphens: auto;
}

.flip-enter-active,
.flip-leave-active {
	transition: transform 0.6s;
}

.flip-enter-from {
	transform: rotateY(90deg);
}

.flip-enter-to {
	transform: rotateY(0deg);
}

.flip-leave-from {
	transform: rotateY(0deg);
}

.flip-leave-to {
	transform: rotateY(-90deg);
}
</style>
