<script setup>
import { ref, watch } from 'vue'
import Card from './Card.vue'
import Button from './Button.vue'
import Popup from './Popup.vue'

const cardsArr = ref([
	{
		text: 'Документы работника при приеме на работу',
		isCorrect: true,
		reverseSideText: 'Верно. Перечень документов перечислен в статье 65 ТК РФ',
	},
	{
		text: 'Дополнительные документы работника',
		isCorrect: true,
		reverseSideText:
			'Верно. Работодатель может попросить дополнительные документы от работника',
	},
	{
		text: 'Ознакомить работника с ЛНА',
		isCorrect: true,
		reverseSideText:
			'Верно. До подписания договора нужно ознакомить с ЛНА. Статья 68 ТК РФ',
	},
	{
		text: 'Заключить трудовой договор',
		isCorrect: true,
		reverseSideText: 'Верно. Прием на работу оформляется трудовым договором',
	},
	{
		text: 'Приказ о приеме на работу',
		isCorrect: false,
		reverseSideText:
			'На основании ФЗ от 22.11.2021 № 377-ФЗ оформлять приказ о приеме на работу необязательно',
	},
	{
		text: 'Личная карточка по форме Т-2',
		isCorrect: false,
		reverseSideText:
			'Постановлением Правительства РФ от 24.07.2021 № 1250 обязанность вести форму Т-2 отменена',
	},
	{
		text: ' Запись о приеме на работу',
		isCorrect: true,
		reverseSideText:
			' Верно. Внести запись о приеме на работу в трудовую книжку или сведения о трудовой деятельности',
	},
	{
		text: 'Отчет о приеме работника',
		isCorrect: true,
		reverseSideText:
			'Верно. Нужно оправить отчет в СФР и уведомление в военкомат (на военнообязанного работника).',
	},
])

const zones = ref([
	{ id: 'zone0', cards: [] },
	{ id: 'zone1', cards: [] },
	{ id: 'zone2', cards: [] },
	{ id: 'zone3', cards: [] },
	{ id: 'zone4', cards: [] },
	{ id: 'zone5', cards: [] },
	{ id: 'zone6', cards: [] },
	{ id: 'zone7', cards: [] },
])

const activeZone = ref(null)

const unCorrectCount = cardsArr.value.filter(
	item => item.isCorrect === false
).length

const onDragEnter = zoneId => {
	activeZone.value = zoneId
}

const onDragLeave = () => {
	activeZone.value = null
}

const isShowCheckBtn = ref(false)

const onDrop = zoneId => {
	isShowCheckBtn.value = true
	if (isChecked.value) return
	const droppedCardText = event.dataTransfer.getData('text')
	const cardIndex = cardsArr.value.findIndex(
		card => card.text === droppedCardText
	)

	if (cardIndex !== -1) {
		const [droppedCard] = cardsArr.value.splice(cardIndex, 1)
		const targetZone = zones.value.find(zone => zone.id === zoneId)

		// Проверяем, пустая ли зона
		if (targetZone && targetZone.cards.length === 0) {
			targetZone.cards.push(droppedCard)
		} else {
			cardsArr.value.push(droppedCard)
		}
	}

	activeZone.value = null
}

const isChecked = ref(false)
const isShowPopup = ref(false)
const isCorrect = ref(null)

const checkUserAnswer = () => {
	isChecked.value = true
	setTimeout(() => {
		isShowPopup.value = true
	}, 4000)

	isCorrect.value =
		unCorrectCount ===
			cardsArr.value.filter(item => item.isCorrect === false).length &&
		cardsArr.value.length === unCorrectCount
}
</script>

<template>
	<div class="container">
		<div v-if="isShowPopup">
			<Popup :isCorrect="isCorrect" />
		</div>
		<div class="container__img">
			<img src="../assets/stend.jpg" alt="" />
			<div
				v-for="zone in zones"
				:key="zone.id"
				class="drop-zone"
				:id="zone.id"
				:class="{ 'drop-zone--active': activeZone === zone.id }"
				@dragover.prevent
				@dragenter="onDragEnter(zone.id)"
				@dragleave="onDragLeave"
				@drop="onDrop(zone.id)"
			>
				<div v-for="card in zone.cards" :key="card.text" class="dropped-card">
					<Card
						min-height="30vw"
						:text="card.text"
						:reverse-side-text="card.reverseSideText"
						:isCorrect="card.isCorrect"
						:isChecked="isChecked"
					/>
				</div>
			</div>
		</div>

		<div class="container__cards">
			<Card
				v-for="item in cardsArr"
				:key="item.text"
				:text="item.text"
				:reverse-side-text="item.reverseSideText"
			/>
		</div>

		<div v-if="!isChecked && isShowCheckBtn" class="container__btn">
			<Button @click="checkUserAnswer">ПРОВЕРИТЬ</Button>
		</div>
	</div>
</template>

<style scoped>
.container__img {
	position: relative;
}
.container__img img {
	width: 100vw;
	z-index: -100;
}

.drop-zone {
	position: absolute;
	width: 19.5vw;
	height: 29.6vw;
}

#zone0 {
	top: 14vw;
	left: 4.8vw;
}

#zone1 {
	top: 14vw;
	left: 28.5vw;
}

#zone2 {
	top: 14vw;
	left: 52.1vw;
}

#zone3 {
	top: 14vw;
	left: 75.7vw;
}

#zone4 {
	top: 47.5vw;
	left: 4.8vw;
}

#zone5 {
	top: 47.5vw;
	left: 28.5vw;
}

#zone6 {
	top: 47.5vw;
	left: 52.1vw;
}

#zone7 {
	top: 47.5vw;
	left: 75.7vw;
}

.container__cards {
	margin: 1vw 2vw;
	display: grid;
	grid-template-columns: repeat(4, 1fr);
	gap: 2vw;
}

.container__btn {
	margin-top: 2vw;
	display: flex;
	justify-content: center;
}
</style>
