<script setup>
import { computed } from 'vue'
import Button from './Button.vue'

const props = defineProps({
	isCorrect: {
		type: Boolean,
		required: true,
	},
})

const text = computed(() => {
	if (props.isCorrect) {
		return 'ВЫ ОТЛИЧНО СПРАВИЛИСЬ С ЗАДАНИЕМ!\n ЧТОБЫ ПРОЙТИ  ТРЕНИРОВКУ ЕЩЕ РАЗ НАЖМИТЕ КНОПКУ ПРОЙТИ СНОВА'
	} else {
		return 'Хороший результат, вы справились с заданием, но выбрали в том числе не обязательные документы. \n Пройдите задание еще раз, чтобы помочь Полине сделать верную памятку. Пройти снова?'
	}
})

const reload = () => {
	location.reload()
}
</script>

<template>
	<div class="container">
		<div :class="{ good: isCorrect, bad: !isCorrect }" class="container__popup">
			{{ text }}
			<div>
				<div class="container__btn">
					<Button @click="reload" font-size="0.8em">ПРОЙТИ СНОВА </Button>
				</div>
			</div>
		</div>
	</div>
</template>

<style scoped>
.container {
	position: absolute;
	width: 100vw;
	height: 123.3vw;
	background: rgba(0, 0, 0, 0.4);
	backdrop-filter: blur(0.6vw);
	z-index: 999;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
}

.container__popup {
	width: 50vw;
	white-space: pre-line;
	padding: 2vw;
	text-align: justify;
	background-color: #fafafa;
	border-radius: 2vw;
	font-size: 1.1em;
}

.good {
	color: #00b050;
}

.bad {
	color: red;
}

.container__btn {
	margin-top: 1vw;
	display: flex;
	justify-content: center;
}
</style>
